
.toUpdateWrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}
.toUpdateBox {
  position: absolute;
  width: 17.66666667rem;
  background-color: transparent;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.updateBg {
  width: 100%;
  height: 9.33333333rem;
  background: url("http://hly.1000da.com.cn/assets/img/toUpdateBg.png") no-repeat center center;
  background-size: cover;
  border-radius: 0.66666667rem 0.66666667rem 0 0;
}
.updatePlane {
  width: 3.43333333rem;
  height: 7rem;
  background: url("http://hly.1000da.com.cn/assets/img/plane.png") no-repeat;
  background-size: 100% 100%;
  position: absolute;
  top: -2.26666667rem;
  left: 50%;
  margin-left: -1.7rem;
  animation: rotatePlane 2s infinite;
  -moz-animation: rotatePlane 2s infinite;
  -webkit-animation: rotatePlane 2s infinite;
  -o-animation: rotatePlane 2s infinite;
}
.updatePlane.active {
  animation: myfirst 2s;
  -moz-animation: myfirst 2s;
  -webkit-animation: myfirst 2s;
  -o-animation: myfirst 2s;
}
@-webkit-keyframes rotatePlane {
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@keyframes rotatePlane {
50% {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
100% {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
}
}
@-webkit-keyframes myfirst {
30% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
100% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
}
}
@keyframes myfirst {
30% {
    -webkit-transform: translateY(40px);
            transform: translateY(40px);
}
100% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
}
}
.updateBg > i {
  position: absolute;
  width: 1.56666667rem;
  height: 1.56666667rem;
  right: 0.66666667rem;
  top: -3rem;
  background: url("assets/img/home/closeUpdate.png") no-repeat;
  background-size: 100% 100%;
}
.toUpdateContent {
  padding: 0.33333333rem 1.16666667rem 1rem;
  background-color: #fff;
  margin-top: -1px;
  border-radius: 0 0 0.66666667rem 0.66666667rem;
}
.aboutEdition {
  font: bold 0.93 30rem/1.66rem "微软雅黑";
  color: #333;
}
.aboutEdition > span {
  display: block;
}
.newContent {
  margin-top: 0.66666667rem;
  font: bold 0.93 30rem/1.66rem "微软雅黑";
  color: #666;
}
.newContent > * {
  display: block;
}
.toUpdateContent > a {
  display: block;
  margin-top: 1.66666667rem;
  background-color: #f8921a;
  color: #ffffff;
  font: 1rem/2.66rem "微软雅黑";
  text-align: center;
}



